.result-header {
	display: flex;
	justify-content: center;
	gap: 5px;
	font-size: x-large;
}

.result-resultcolor-red {
	font-size: x-large;
	color: red;
}
.result-resultcolor-yellow {
	font-size: x-large;
	color: yellow;
}

.result-list-all {
	display: grid;
	grid-template-columns: repeat(2, 1fr); /* 3列のグリッドを作成 */
	grid-gap: 10px; /* グリッドアイテム間の隙間 */
	color: white;
}

.result-list-element {
	background-color: #0d61af;
	border: 1px solid white;
	border-radius: 10%;
}

.result-list-flex {
	display: flex;
	gap: 5px;
	margin-bottom: 10px;
}
